.main-app {
  display: grid;
  grid-template-areas: "header header . ."
                        "tools frames draw anim";
  grid-template-rows: 40px;
  grid-template-columns: 10% 10% 60% 20%;
  grid-row-gap: 10px;
  background-color: #3d3d3d;
  width: 100%;
  min-height: 100vh;
  text-align: center;
  color: #ffffff;
}
