.frames {
  grid-area: frames;
}

.frames__container {
  height: 750px;
  overflow: overlay;
}

.frames__buttons {
  display: flex;
  flex-direction: column;
  margin-left: 28px;
}

.frames__add-frame, .frames__revers-frame{
  outline: none;
  border: 3px solid #808080;
  border-radius: 3px;
  background: none;
  color: #808080;
  font-weight: bold;
  width: 134px;
  height: 23px;
  margin-bottom: 5px;
}

.frames__add-frame:hover, .frames__revers-frame:hover {
  border-color: #ffd700;
}
