.single-frame {
  position: relative;
}

.single-frame_canvas {
  background-color: #a9a9a9;
  border: 3px solid #868686;
  border-radius: 3px;
}

.single-frame_index {
  background-color: #ffd700;
  width: 30px;
  height: 30px;
  line-height: 30px;
  position: absolute;
  top: 3px;
  left: 31px;
  border-radius: 3px;
}

.highlight {
  border-color: #ffd700;
  z-index: 10;
}

.single-frame_delete {
  background-color: #999999;
  opacity: 0.2;
  width: 30px;
  height: 30px;
  line-height: 30px;
  position: absolute;
  top: 3px;
  left: 129px;
  border-radius: 3px;
  font-size: 20px;
}

.single-frame_delete:hover, .single-frame_double:hover {
  background-color: #111111;
  opacity: 0.5;
  color: white;
  font-weight: bold;
}

.single-frame_double {
  background-color: #999999;
  opacity: 0.2;
  width: 30px;
  height: 30px;
  line-height: 30px;
  font-size: 20px;
  position: absolute;
  top: 101px;
  left: 129px;
  border-radius: 3px;
}

